      if (orders.length === 0) {
        this.$buefy.dialog.alert('‡πÑ‡∏°‡πà‡∏°‡∏µ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£');
      } else {
        const text = orders
          .map((each: Order, index: number) => {
