            const ownerLabel = each.owner ? `‡∏Ç‡∏≠‡∏á ${each.owner}` : '';
            return `<li>
            ${index + 1}.
            ${each.name}
            ${bubbleLabel.label}
            ${sweetnessLabel.label}
            ${each.amount} ‡πÅ‡∏Å‡πâ‡∏ß
            ${ownerLabel}
            </li>`;
