import Vue from 'vue';
import VueRouter from 'vue-router';

import MenuPage from '../views/MenuPage.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'menu',
    component: MenuPage,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
