





























import { Component, Vue } from 'vue-property-decorator';
import store from '@/store/store';
import { Order } from '@/store/state.interface';
import {
  newMenu,
  milkTea,
  blackTea,
  thaiTea,
  soda,
  otherDrink,
  sweetness,
  bubble,
} from '@/database/menu';
import DrinkItem from '@/interfaces/DrinkItem';
import DrinkList from '@/components/DrinkList.vue';
import OrderList from '@/components/OrderList.vue';

@Component({
  components: {
    DrinkList,
    OrderList,
  },
  data() {
    return {
      newMenu,
      milkTea,
      blackTea,
      thaiTea,
      soda,
      otherDrink,
      orders: this.$store.state.orders,
    };
  },
  methods: {
    addOrder(drink: DrinkItem) {
      const order = {
        id: new Date().getTime(),
        name: drink.name,
        bubble: bubble[0].value,
        sweetness: sweetness[0].value,
        amount: 1,
        owner: '',
      } as Order;
      store.dispatch('addOrder', order);
    },
    updateOrder(payload: Order) {
      store.dispatch('updateOrder', payload);
    },
    removeOrder(e) {
      store.dispatch('removeOrder', e.id);
    },
  },
})
export default class MenuPage extends Vue {}
