













































import { Component, Vue, Prop } from 'vue-property-decorator';
import { Order, Sweetness, Bubble } from '@/store/state.interface';
import { sweetness, bubble } from '@/database/menu';

@Component
export default class OrderItem extends Vue {
  @Prop()
  public order!: Order;

  sweetnessList: Sweetness[] = sweetness;

  bubbleList: Bubble[] = bubble;

  innerOrder: Order = this.order;

  inputBubble(value: string) {
    this.innerOrder.bubble = value;
  }

  inputSweetness(value: string) {
    this.innerOrder.sweetness = value;
  }

  inputAmount(value: number) {
    this.innerOrder.amount = value;
  }

  inputOwner(value: string) {
    this.innerOrder.owner = value;
  }

  updateOrder(id: number) {
    const payload = { ...this.innerOrder } as Order;
    this.$emit('updateOrder', payload);
  }

  removeOrder(id: number) {
    this.$emit('removeOrder', { id });
  }
}
